import Vue from 'vue'
import Vuex from 'vuex'
import { pageComponents } from "./page_components.js"
import { state, getters, mutations, actions } from "./app_store.js"
import { tb_wali } from "./tb_wali.js"
import { tb_kelas } from "./tb_kelas.js"
import { tb_tipe_pelanggaran } from "./tb_tipe_pelanggaran.js"
import { tb_guru } from "./tb_guru.js"
import { tb_siswa } from "./tb_siswa.js"
import { tb_pelanggaran } from "./tb_pelanggaran.js"
import { laporan } from "./laporan.js"
Vue.use(Vuex)
export default function (/* { ssrContext } */) {
	const Store = new Vuex.Store({
		state,
		getters,
		mutations,
		actions,
		modules: {
			pageComponents,
			tb_wali,
		tb_kelas,
		tb_tipe_pelanggaran,
		tb_guru,
		tb_siswa,
		tb_pelanggaran,
		laporan
		},
		// enable strict mode (adds overhead!)
		// for dev mode only
		strict: process.env.DEV
	})
	return Store
}